import axios from 'axios';
import {
  FormBtn,
  FormInputBox,
  HiddenInput,
  Input,
  InputNote,
  Label,
} from 'components/LeadForm/LeadForm.styled';
import { LogoRoute } from 'components/Menu/Menu.styled';
import { Loader } from 'components/SharedLayout/Loaders/Loader';
import { Formik } from 'formik';
import { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import * as yup from 'yup';
import {
  FacebookBtn,
  InstagramBtn,
  SocialLogoLink,
  SocialsLinkWrapper,
  TikTokBtn,
  YouTubeBtn,
} from '../ThankYouPage/ThankYouPage.styled';
import {
  ContactPhone,
  ContactPhoneNumber,
  FormBackground,
  HeaderWrapper,
  LeadFormAddTextNew,
  Logo,
  LogoMobile,
  PageForm,
  PageFormHeading,
  PageFormWrapper,
  PhoneNumber,
  SocialsBoxNew,
  SocialsBoxNewMobile,
  SocialsTextNew,
  ThankYouHeaderNew,
  ThankYouSectionNew,
} from './UniversalLeadFormPage.styled';
import { BackgroundFilterBottom, BackgroundFilterBottomLeft, BackgroundFilterTop, BackgroundFilterTopRight } from 'pages/Quiz/Quiz.styled';

axios.defaults.baseURL = 'https://ap-server-8qi1.onrender.com';

const UniversalLeadFormPage = ({ utms }) => {
  const [isLoading, setIsLoading] = useState(false);
  const navigate = useNavigate();

  useEffect(() => {
    document.title = '  | AP Education';
  }, []);

  const initialValues = {
    name: '',
    phone: '',
    tag: '',
    utm_content: '',
    utm_medium: '',
    utm_campaign: '',
    utm_source: '',
    utm_term: '',
    utm_referrer: '',
    referrer: '',
    gclientid: '',
    gclid: '',
    fbclid: '',
  };

  const leadSchema = yup.object().shape({
    name: yup
      .string()
      .required(" ,   '  !")
      .matches(
        /^[A-Za-z--]+(?:[-'\s][A-Za-z--]+)+$/,
        " ,  '  ,    ,    !"
      )
      .min(2, '     2 !')
      .max(50, '     50 !'),
    phone: yup
      .string()
      .required(' ,    !')
      .matches(
        /\+?\d{1,4}?[-.\s]?\(?\d{1,3}?\)?[-.\s]?\d{1,4}[-.\s]?\d{1,4}[-.\s]?\d{1,9}/,
        ' ,    !'
      )
      .min(10, '        10 !')
      .max(18, '        18 !'),
    time: yup
      .string()
      .required(
        ' ,     ,     !'
      ),
    tag: yup.string().optional(),
    utm_content: yup.string().optional(),
    utm_medium: yup.string().optional(),
    utm_campaign: yup.string().optional(),
    utm_source: yup.string().optional(),
    utm_term: yup.string().optional(),
    utm_referrer: yup.string().optional(),
    referrer: yup.string().optional(),
    gclientid: yup.string().optional(),
    gclid: yup.string().optional(),
    fbclid: yup.string().optional(),
  });

  const handleSubmit = async (values, { resetForm }) => {
    values.tag = utms.utm_source;
    values.utm_content = utms.utm_content;
    values.utm_medium = utms.utm_medium;
    values.utm_campaign = utms.utm_campaign;
    values.utm_source = utms.utm_source;
    values.utm_term = utms.utm_term;
    values.utm_referrer = utms.utm_referrer;
    values.referrer = utms.referrer;
    values.gclientid = utms.gclientid;
    values.gclid = utms.gclid;
    values.fbclid = utms.fbclid;
    setIsLoading(isLoading => (isLoading = true));

    try {
      const response = await axios.post('/leads', values);
      console.log(response);
      resetForm();
      navigate('/thankyou');
    } catch (error) {
      console.error(error);
    } finally {
      setIsLoading(isLoading => (isLoading = false));
    }
  };

  return (
    <FormBackground>
      <ThankYouHeaderNew>
        <HeaderWrapper>
          <LogoRoute to="/">
            <LogoMobile />
            <Logo />
          </LogoRoute>
          <PhoneNumber href="tel:+380936707129">
            <ContactPhone />
            <ContactPhoneNumber>+38 (093) 670 71 29</ContactPhoneNumber>
          </PhoneNumber>
        </HeaderWrapper>
      </ThankYouHeaderNew>
      <ThankYouSectionNew>
        <PageFormWrapper>
          <PageFormHeading>
            {utms.utm_source === 'apstarshak'
              ? '    -15%    APStarshak!'
              : '         !'}
          </PageFormHeading>
          <LeadFormAddTextNew>
            P. S.     
          </LeadFormAddTextNew>
          <SocialsBoxNew>
            <SocialsTextNew>
                    :
            </SocialsTextNew>
            <SocialsLinkWrapper>
              <SocialLogoLink
                href="https://www.instagram.com/ap.education/"
                target="_blank"
                rel="noopener noreferrer"
              >
                <InstagramBtn />
              </SocialLogoLink>
              <SocialLogoLink
                href="https://www.facebook.com/ap.edu.centre/"
                target="_blank"
                rel="noopener noreferrer"
              >
                <FacebookBtn />
              </SocialLogoLink>
              <SocialLogoLink
                href="https://www.tiktok.com/@ap.education.center"
                target="_blank"
                rel="noopener noreferrer"
              >
                <TikTokBtn />
              </SocialLogoLink>
              <SocialLogoLink
                href="https://www.linkedin.com/company/ap-education-center/"
                target="_blank"
                rel="noopener noreferrer"
              >
                <YouTubeBtn />
              </SocialLogoLink>
              <SocialLogoLink
                href="https://www.youtube.com/channel/UC3XSGAVLhPXXlMN5-Gebtvw"
                target="_blank"
                rel="noopener noreferrer"
              >
                <YouTubeBtn />
              </SocialLogoLink>
            </SocialsLinkWrapper>
          </SocialsBoxNew>
        </PageFormWrapper>
        <Formik
          initialValues={initialValues}
          onSubmit={handleSubmit}
          validationSchema={leadSchema}
        >
          <PageForm>
            <FormInputBox>
              <Label>
                <Input
                  type="text"
                  name="name"
                  placeholder="'  *"
                  autocomplete="off"
                />
                <InputNote component="p" name="name" />
              </Label>
              <Label>
                <Input
                  type="tel"
                  name="phone"
                  placeholder="*"
                  autocomplete="off"
                />
                <InputNote component="p" name="phone" />
              </Label>
              <Label>
                <Input
                  type="text"
                  name="time"
                  placeholder="  ?*"
                  autocomplete="off"
                />
                <InputNote component="p" name="time" />
              </Label>
            </FormInputBox>
            <HiddenInput type="text" name="tag" />
            <HiddenInput type="text" name="utm_content" />
            <HiddenInput type="text" name="utm_medium" />
            <HiddenInput type="text" name="utm_campaign" />
            <HiddenInput type="text" name="utm_source" />
            <HiddenInput type="text" name="utm_term" />
            <HiddenInput type="text" name="utm_referrer" />
            <HiddenInput type="text" name="referrer" />
            <HiddenInput type="text" name="gclientid" />
            <HiddenInput type="text" name="gclid" />
            <HiddenInput type="text" name="fbclid" />
            <FormBtn type="submit"></FormBtn>
            {isLoading && <Loader />}
          </PageForm>
        </Formik>
        <SocialsBoxNewMobile>
          <SocialsTextNew>
                  :
          </SocialsTextNew>
          <SocialsLinkWrapper>
            <SocialLogoLink
              href="https://www.instagram.com/ap.education/"
              target="_blank"
              rel="noopener noreferrer"
            >
              <InstagramBtn />
            </SocialLogoLink>
            <SocialLogoLink
              href="https://www.facebook.com/ap.edu.centre/"
              target="_blank"
              rel="noopener noreferrer"
            >
              <FacebookBtn />
            </SocialLogoLink>
            <SocialLogoLink
              href="https://www.tiktok.com/@ap.education.center"
              target="_blank"
              rel="noopener noreferrer"
            >
              <TikTokBtn />
            </SocialLogoLink>
            <SocialLogoLink
              href="https://www.linkedin.com/company/ap-education-center/"
              target="_blank"
              rel="noopener noreferrer"
            >
              <YouTubeBtn />
            </SocialLogoLink>
            <SocialLogoLink
              href="https://www.youtube.com/channel/UC3XSGAVLhPXXlMN5-Gebtvw"
              target="_blank"
              rel="noopener noreferrer"
            >
              <YouTubeBtn />
            </SocialLogoLink>
          </SocialsLinkWrapper>
        </SocialsBoxNewMobile>
        <BackgroundFilterTop /> <BackgroundFilterBottom />
        <BackgroundFilterTopRight />
        <BackgroundFilterBottomLeft />
      </ThankYouSectionNew>
    </FormBackground>
  );
};

export default UniversalLeadFormPage;
